@keyframes move {
  25% {
    top: 0;
    left: calc(100% - 100px);
    border-radius: 0;
    background-color: red;
    transform: rotate(180deg);
  }
  50% {
    top: calc(100% - 100px);
    left: calc(100% - 100px);
    border-radius: 100%;
    background-color: blue;
    transform: rotate(0);
  }
  75% {
    top: calc(100% - 100px);
    left: 0;
    border-radius: 0;
    background-color: red;
    transform: rotate(180deg);
  }
}

.Native {
  position: absolute;
  animation: move 20s infinite linear;
  background-color: blue;
  display: inline-block;
  width: 100px;
  height: 100px;
  border-radius: 100%;
  transform: rotate(0);
  top: 0;
  left: 0;
}
